.signUpScreen >form{
  margin-top:30px;
    max-width: 300px;
    padding: 70px;
    padding-top: 10px;
    padding-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
    background: #151719;
    border-radius: 3%;
  }
  .signUpScreen > form > h1 {
    color:#64c;
    text-align: left;
    margin-bottom: 25px;
  }
  .signUpScreen > form > h4 {
    text-align: left;
    margin-top: 30px;
  }
  .signUpScreen_gray {
    color: #ECEDED;
  }
  .signUpScreen > form {
    display: grid;
    flex-direction: column;
  }
  .signUpScreen > form > input {
    outline-width: 0;
    height: 40px;
    margin-bottom: 14px;
    border-radius: 5px;
    border: none;
    padding: 5px 15px;
  }
  .signUpScreen > form > button {
    padding: 16px 20px;
    font-size: 1rem;
    color: #fff;
    border-radius: 5px;
    background-color:#64c;
    font-weight: 600;
    border: none;
    cursor: pointer;
    margin-top: 20px;
  }

  .signUpScreen_link{
    color: #64C9AD;
  }
  .signUpScreen_link:hover {
    cursor: pointer;
    text-decoration: underline;
  }
  